h1 {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
}

#cube .front {
    transform:
            translateZ(100px);
}

#cube .back {
    transform:
            rotateX(-180deg)
            translateZ(100px);
}

#cube .right {
    transform:
            rotateY(90deg)
            translateZ(100px);
}

#cube .left {
    transform:
            rotateY(-90deg)
            translateZ(100px);
}

#cube .top {
    transform:
            rotateX(90deg)
            translateZ(100px);
}

#cube .bottom {
    transform:
            rotateX(-90deg)
            translateZ(100px);
}

.container {
    width: 200px;
    height: 200px;
    position: relative;
    margin: 0 auto 40px;
    border: 1px solid #FFF;

    perspective: 10000px;
    perspective-origin: 50% 100%;
}

#cube {
    width: 100%;
    height: 100%;
    top: 100px;
    position: absolute;

    transform-style: preserve-3d;

    transition: transform 2s;
}

#cube:hover {
    cursor: pointer;
}

#cube div {
    background: white;
    display: block;
    position: absolute;
    width: 200px;
    height: 100px;
    border: 2px solid black;
    border-radius: 10px;

    margin: 0 auto;

    text-align: center;
    padding: 50px 0;
}

.dot {
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    background: black;
    border-radius: 15px;
}

.front .dot1 { top: 85px; left: 85px; }
.back .dot1 { top: 45px; left: 45px; }
.back .dot2 { top: 125px; left: 125px; }
.right .dot1 { top: 45px; left: 45px; }
.right .dot2 { top: 85px; left: 85px; }
.right .dot3 { top: 125px; left: 125px; }
.left .dot1 { top: 45px; left: 45px; }
.left .dot2 { top: 45px; left: 125px; }
.left .dot3 { top: 125px; left: 45px; }
.left .dot4 { top: 125px; left: 125px; }
.top .dot1 { top: 45px; left: 45px; }
.top .dot2 { top: 45px; left: 125px; }
.top .dot3 { top: 85px; left: 85px; }
.top .dot4 { top: 125px; left: 45px; }
.top .dot5 { top: 125px; left: 125px; }
.bottom .dot1 { top: 45px; left: 45px; }
.bottom .dot2 { top: 45px; left: 85px; }
.bottom .dot3 { top: 45px; left: 125px; }
.bottom .dot4 { top: 125px; left: 45px; }
.bottom .dot5 { top: 125px; left: 85px; }
.bottom .dot6 { top: 125px; left: 125px; }
